import React, { Component } from 'react';
import { Row, Col, Form, Select, Checkbox } from 'antd';
import { BtnSearch, BtnClear } from 'components/common/button';
import FilterPathEducate from 'components/common/input/FilterPathEducate';

const { Option } = Select;

@Form.create({ name: 'formScholarshipApproval' })
class YearlyClass extends Component {
  state = {};

  handleReset = () => {
    this.props.form.resetFields();
  };

  handleSearch = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      console.log('Received values of form: ', values);
    });
  };

  render() {
    const { getFieldDecorator } = this.props.form;

    return (
      <Form layout="vertical" className="ant-advanced-search-form" onSubmit={this.handleSearch}>
        <Row gutter={24}>
          <FilterPathEducate form={this.props.form} classes={{ isShow: false }} />
          <Col span={8}>
            <Form.Item label="Hc k">
              {getFieldDecorator('hk', {})(
                <Select>
                  <Option value="1">02</Option>
                </Select>
              )}
            </Form.Item>
          </Col>
          <Col span={8}>
            <Form.Item label="Nm hc">
              {getFieldDecorator('nh', {})(
                <Select>
                  <Option value="1">2014-2015</Option>
                </Select>
              )}
            </Form.Item>
          </Col>
          <Col span={8}>
            <Form.Item label="Ln thi">
              {getFieldDecorator('lt', {})(
                <Select>
                  <Option value="1">01</Option>
                </Select>
              )}
            </Form.Item>
          </Col>
          <Col span={8}>
            <Form.Item label="Hc phn">
              {getFieldDecorator('hp', {})(
                <Select>
                  <Option value="1">Cng tc vn th v lu tr</Option>
                </Select>
              )}
            </Form.Item>
          </Col>
          <Col span={8}>
            <Form.Item label="im ca hc k">
              {getFieldDecorator('dhk', {})(
                <Select>
                  <Option value="1">02</Option>
                </Select>
              )}
            </Form.Item>
          </Col>
          <Col span={8}>
            <Form.Item label="Nm hc">
              {getFieldDecorator('nh', {})(
                <Select>
                  <Option value="1">2014-2015</Option>
                </Select>
              )}
            </Form.Item>
          </Col>
        </Row>

        <Row gutter={24}>
          <Col span={8}>
            <Form.Item>
              {getFieldDecorator('sxt')(<Checkbox>Hin th tt c sinh vin</Checkbox>)}
            </Form.Item>
          </Col>
          <Col span={8}>
            <Form.Item>
              {getFieldDecorator('sxt')(<Checkbox>Tnh li tt c im cho sinh vin</Checkbox>)}
            </Form.Item>
          </Col>
        </Row>
        <Row>
          <Col span={24} style={{ textAlign: 'right' }}>
            <div className="btn-group">
              <BtnSearch />
              <BtnClear />
            </div>
          </Col>
        </Row>
      </Form>
    );
  }
}

export default YearlyClass;



